$itemAfterBgcolor: white;

.main-tab-bar {
  padding: 20px 0;

  .item {
    margin-right: 10px;
    color: #835E1D;
    text-decoration: none;
    position: relative;
  }

  .active::after {
    content: "";
    display: inline-block;
    width: 80%;
    height: 5px;
    background-color: $itemAfterBgcolor;
    position: absolute;
    left: 10%;
    bottom: -6px;
    border-radius: 2.5px;
    z-index: -1;
  }

  .item:active {
    color: #D85916;
  }

  .item:last-child {
    margin-right: 0;
  }
}